// initial state
// shape: [{ id, quantity }]
const state = () => ({
  defaultIMG: require("@/assets/ProjectImg/bg.png"),
  backgroundImage: require("@/assets/ProjectImg/bg.png"),
  OpenWinodow: false,
  ChangeType: 1,
  AticleParams: {
    CategoryID: null,
    PostTime: true,
    LiuLang: true,
    DianZhan: true,
  },
});

// getters
const getters = {};

// actions
const actions = {
  updatedCategory({ commit }, categoryId) {
    commit("setCategoryID", categoryId);
  },
  updatedsetFitterData({ commit }, FitterData) {
    commit("setFitterData", FitterData);
  },
};

// mutations
const mutations = {
  setBackground(state, newImage) {
    state.backgroundImage = newImage;
  },
  setChangeWindows(state, ChangeType) {
    state.OpenWinodow = ChangeType.Open;
    state.ChangeType = ChangeType.Type;
  },
  setCategoryID(state, CategoryID) {
    state.AticleParams.CategoryID = CategoryID;
  },
  setFitterData(state, FitterData) {
    const { PostTime, LiuLang, DianZhan } = FitterData;
    state.AticleParams.PostTime = PostTime;
    state.AticleParams.LiuLang = LiuLang;
    state.AticleParams.DianZhan = DianZhan;
  },
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};
